
.header{
    background: #131921;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
}

.header_logo{
    width: 100px;
    object-fit: contain;
    margin-left: 20px;    
    margin-right: 20px;
    margin-top: 18px;
    
}

.header_optionBasket{
    display: flex;
    align-items: center;
}

.header_basketCount{
  margin-left: 10px;  
  margin-right: 10px;
}

.header_link{
    color: #ffffff;
    text-decoration: none;
}

.header_nav{
    display: flex;
    justify-content: space-evenly;
}

.header_option{
    display: flex;
    flex-direction: column;
    margin-right: 10px;
    margin-left: 10px;
}

.header_optionLineOne{
    font-size: 10px;
}

.header_optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}


.header_search{
    display: flex;
    flex: 1;
   
}

.header_searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header_searchIcon{
    background: #cd9842;
    padding: 5px;
    height: 22px !important;
}
