.product{
    display: flex;
    flex-direction: column;
    max-height: 400px;
    min-width: 100px;
    background: #ffffff;
    padding: 20px;
    justify-content: flex-end;
    align-items: center;
    margin: 10px;
    width: 100%;
    z-index: 1;
}

.product_info{
    height: 100px;
    margin-bottom: 15px;
}

.product_price{
    margin-top: 15px;
}

.product_rating{
    display: flex;
    color: #f0c14b;
    font-size: 25px;
}

.product > img{
    width: 100%;
    max-height: 200px;
    object-fit: contain;
    margin-bottom: 15px;
}

.product > button{
    background: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
}
