.login{
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login_logo{
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
   width: 100px; 
   margin-right: auto;
   margin-left: auto;
}

.login-container{
    width: 300px;
    display: flex;
    flex-direction: column;
    height: fit-content;
    border: 1px solid #aaaaaa;
    padding: 20px;
    border-radius: 5px;
}

.login-container > h1{
    font-weight: 500;
    margin-bottom: 20px;
}

.login-container > form > h5{
    margin-bottom: 5px;
}

.login-container > form > input{
    height: 30px;
    margin-bottom: 10px;
    width: 98%;
    background-color: #ffffff;
}

.login-container > p{
    margin-top: 15px;
    font-size: 12px;
}

.login_signinbutton{
    width: 100%;
    background: #f0c14b;
    border-radius:2px;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    margin-top: 10px;
    height: 30px;
}

.login_registerbutton{
    width: 100%;
    background: #e4dede;
    border-radius:2px;
    border: 1px solid #aaaaaa;
    margin-top: 10px;
    height: 30px;
}